const FaqBanner=()=>{
    return(
        <section id="banner">
  <div className="two_column">
    <div className="column_left">
      <h1 className="heading isLoaded">
        <span> Frequently Asked Questions</span>
        {/* <span>Frequently Asked Questions</span> */}
      </h1>
      <p className="sub-heading  p-14 isLoaded">Lorem ipsum dolor sit amet consectetur adipisicing elit. Ipsam tenetur neque necessitatibus dicta dignissimos amet aspernatur pariatur facilis magni. Asperiores!</p>
    </div>
    <div className="column_right isLoaded">
      <img src={Faq_BannerImg} width="320px" alt="" />
    </div>
  </div>
</section>

    )
}

const FaqSearch=()=>{
    return(
        <div className="search-form full-width-form  bottom-margin">
  <div className>
    <button className="--no"><span className="fa fa-search" /></button>
    <input type="text" placeholder="Search for Anything about Chop" name="faq-search" className="input-box" id="faq-search" />
  </div>
  <button className="--no cancel-input"> <span role="img">×</span></button>
</div>

    )
}

const CantFindQuest=()=>{
    return(
        <div className="two_column more-faq">
  <div className="column_left">
    <img src={Faq_MoreImg} width={500} alt="" />
  </div>
  <div className="column_right ">
    <h2 className="heading color">Not What You Need?</h2>
    <p className="p-16 sub-heading">
      Our team is happy to answer your questions. Contact us and we'll be in touch as soon as possible. 
      Ss animi illo officiis assumenda mollitia in facere modi
    </p>
    <br />
    <button>Contact Us</button>
  </div>
</div>

    )
}

