import {useState} from 'react'
import Envelope from '../images/envelope-open.svg'




function Cta(){
  const [formError,updateError]=useState(false)
  
function submitForm(e){
    const target=e.target[0];
    target.style.opacity=1;
    
if(!target.checkValidity() ||  target.value =="" ){
  e.preventDefault()
}else{
  const urlencoded = new URLSearchParams(),
  endpoint="https://chop1.herokuapp.com/add/waitlist", 
  requestOptions = {
    method: 'POST',
    body: urlencoded,
    redirect: 'follow'
  };
  urlencoded.append("email", target.value)

fetch(endpoint, requestOptions)
.then(response => response.text())
.then(result => {
updateError(true)
}) 
.catch(error => console.log(error));


}
  }


    return(
        <section id="cta">
  <div className="backgroundImage">
    <div className="two_column">
      <div className="column_left">
        <h2 className="heading" data-aos="fade-down" data-aos-delay={10} data-aos-duration={1000}>Drive your fleet with Coast</h2>
        <p className="sub-heading" data-aos="fade-right" data-aos-delay={10} data-aos-duration={1000}>Get up and running in just a few simple steps.</p>
        <div className="form-container" data-aos="fade-up" data-aos-delay={10} data-aos-duration={1000}>
          <p className="notify-error color"> 
          {formError && 'Invalid Email Address'}
          </p>
          <form method="POST" onSubmit={submitForm} name="notify" className="notify-form">
            <img className="icon" src={Envelope} alt="Email- Envelope" width="20px" />
            <input type="email" autoComplete="off" name="email" id="email" placeholder="Enter your Email Address" />
            <button className="btn">Notify Me</button>
          </form>
        </div>
      </div>
      <div className="column_right">
      </div>
    </div>
  </div>
</section>

    )
}

export default Cta