import VendorImg from '../images/desktop.png';

function VendorDisplay(){
    return(
        <section id="whatsapp-trader" className="vendor-display">
  <div className="__after"> Deliver Foods with <span className="color">Chop Vendor</span></div>
  <div className="two_column">
    <div className="column_left">
      <div className="trader-context" text="test">
        <p data-aos="fade-right" data-aos-delay={10} data-aos-duration={1000}>Lorem ipsum dolor sit amet consectetur adipisicing elit. Ab accusamus molestias voluptatum distinctio explicabo, reiciendis alias sint quaerat exercitationem aliquid sapiente natus deserunt repellat error modi laboriosam mollitia officia quisquam.
        </p>
        <div className="buttons">
          <button className="btn">
            <span>Get started as a Vendor</span>
            <i className="fa fa-users" />
          </button>
        </div>
      </div>
    </div>
    <div className="column_right">
      <div className="relatve">
        <img data-aos="fade-in" data-aos-delay={30} data-aos-duration={1000} className="image-motion" 
        src={VendorImg} alt="Chop Vendor Desktop Display" />
      </div>
    </div>
  </div>
</section>

    )
}

export default VendorDisplay