import {Fragment} from 'react'
import Loader from '../components/Loader'
import Header from '../components/Header'
import Cta from '../components/Cta'
import Footer  from '../components/Footer'

import "./contact.scss"
function Contact(){
return(
<Fragment>
<Loader />
<Header />


<section id="about-hero" className>
  <div className>
    <h1 className="heading font-2x isLoaded">
      Contact Chop.Africa
    </h1>
    <p className="sub-heading isLoaded">Lorem ipsum dolor sit amet consectetur adipisicing elit. Alias, sint iusto ab repellat consectetur qui?
    </p>
    <button className="btn-big isLoaded">
      <span><i className="fa fa-arrow-down" /></span>
    </button>
  </div>
  <div className="img-container isLoaded" id="contact-us">
    <form action={0} name="contact-us-form">
      <h2>How can We Help You?</h2>
      <div className="inputs">
        <div className="input-text">
          <div className="flex">
            <input type="text" name="name" placeholder="Your Name?" id="full-name" />
            <input type="email" placeholder="Your Personal or Work Email?" name="email" id="email" />
          </div>
          <div className="contact-type flex">
            {/* Custom Radio Input */}
            <div className="radio-input"> 
              <div className="custom-radio">
                <input type="radio" name="type" defaultChecked id="contact-type" defaultValue="Support" />
                <span className="active" />
              </div>
              <div className="value">Support</div>
            </div>
            {/* Custom Radio Input End*/}
            {/* Custom Radio Input */}
            <div className="radio-input"> 
              <div className="custom-radio">
                <input type="radio" name="type" id="contact-type" defaultValue="Complaint" />
                <span className="active" />
              </div>
              <div className="value">Complaint</div>
            </div>
            {/* Custom Radio Input End*/}
            {/* Custom Radio Input */}
            <div className="radio-input"> 
              <div className="custom-radio">
                <input type="radio" name="type" id="contact-type" defaultValue="Request" />
                <span className="active" />
              </div>
              <div className="value">Request</div>
            </div>
            {/* Custom Radio Input End*/}
            {/* Custom Radio Input */}
            <div className="radio-input"> 
              <div className="custom-radio">
                <input type="radio" name="type" id="contact-type" defaultValue="Enquire" />
                <span className="active" />
              </div>
              <div className="value">Enquire</div>
            </div>
            {/* Custom Radio Input End*/}
          </div>
          <div className="flex">
            <textarea name placeholder="How can we help you?" id="message" cols={0} rows={10} defaultValue={""} />
          </div>
        </div>
        <div className="input-attach">
          <h3 className="type-title">Send An Attachment</h3>
          <div className="attaches flex">
            {/* Attachment  */}
            <div className="attach-f">
              <div className="attach attach-1">
                <span className="icon"><i className="indicator fa fa-plus" /></span>
                <input type="file" accept="image/*" name="attach-1" id="attach" />
              </div>
              <p className="p-14 filename" />
            </div>
            {/* Attachment End */}
            {/* Attachment  */}
            <div className="attach-f">
              <div className="attach attach-2">
                <span className="icon"><i className="indicator fa fa-plus" /></span>
                <input type="file" accept="image/*" name="attach-2" id="attach" />
              </div>
              <p className="p-14 filename" />
            </div>
            {/* Attachment End */}
            {/* Attachment  */}
            <div className="attach-f">
              <div className="attach attach-3">
                <span className="icon"><i className="indicator fa fa-plus" /></span>
                <input type="file" accept="image/*" name="attach-3" id="attach" />
              </div>
              <p className="p-14 filename" />
            </div>
            {/* Attachment End */}
          </div>
        </div> {/* Input Attach End */}
      </div>
    </form>
  </div>
</section>



<div id="bg-body" className="backgroundImage">
<Cta />
<Footer />
</div>
</Fragment>
)
}

export {Contact}