import Logo from '../images/logo.png'


function Footer(){
    return(
        <footer id="footer" className>
  <ul className="footer-items">
    <li className="f_item">
      <img src={Logo} alt="Chop.Africa Logo" width="60px" />
      <h4 className="opaque" style={{opacity: '.5'}}>Chop.Africa</h4>
      <div className="socials">
      </div>
    </li>
    <li className="f_item">
      <h4 className="head">Company</h4>
      <a href>About Us</a>
      <a href>FAQs</a>
      <a href>Contact Us</a>
    </li> 
    <li className="f_item parallax-layer">
      <h4 className="head">Get in Touch</h4>
      <span className="head">Partnerships &amp; general enquires</span>
      <a href="mailto:hello@chop.africa" className="opaque">hello@chop.africa</a>
      {/* <h5 class="head">Give us a follow</h5> */}
      <ul className="socials">
        <a href><li><span className="fab fa-facebook" /></li></a>
        <a href><li><span className="fab fa-twitter" /></li></a>
        <a href> <li><span className="fab fa-instagram" /></li></a>
      </ul>
    </li>
  </ul>
  <div className="terms">
    <a href="terms-conditions.html">Terms of Service | Privacy Policy</a>
    <p><span role="img">©</span> <span className="year"> All Rights Reserved</span></p>
  </div>
</footer>

    )
}

export default Footer