import {useEffect, useState} from'react'
// import image logo
import Logo from '../images/logo.png';

function Loader(){
// initialize loader progress to 0
let [loadProgress,updateLoadProgress]=useState(0)
    // Set a state of loaded to false 
    let [loaded, toggleLoading] = useState(false);
    let [loadWait, changeLoadWait]=useState(true)
    // Creating a function to load when Dom has been Mounted
    useEffect(()=>{
// loading interval time in miliseconds
const speed_interval=15 ;

changeLoadWait(false)

let loadingIncrease=setInterval(()=>{
    // increase the loading progress by one at specific intervals
updateLoadProgress(loadProgress++)

// checks if loading progress is more than 100%
if(loadProgress >= 100){
    // Clear the interval Function
    clearInterval(loadingIncrease);
    // set loaded to true
    toggleLoading(true);
// 


// init aos lib
// AOS.init({
//     mirror:true,
//   })
}else{
//   do something else
}

},speed_interval);
    },[]); //use effect hook end

    return(
        <section id="loader" style={{
            visibility:loaded ? 'hidden' : 'visible'
        }}>
  <div className>
    <img src={Logo} className="logo" alt="Chop.Africa Logo" width={70} />
    <h5>Loading</h5>
    <div className="loading-status">
      <div className={`loading-bar ${!loadWait && "loading"} `}  style={{width: loadProgress + "%"}} />
    </div>
  </div>
</section>

    )
}


export default Loader