import AppImg from '../images/phone.png'


function AppDisplay(){
    return (
        <section id="app">
  <div className="content">
    <div className="content-image">
      <img src={AppImg} data-aos="fade-down" data-aos-duration={1000} alt="" />
    </div>
    <div className="content-text">
      <h3 className="heading" data-aos="fade-up" data-aos-duration={1000}>
        The easiest way to offer powerful delivery features ?
      </h3>
      <p className="spacing-top" data-aos="fade-up" data-aos-duration={1000}>Lorem ipsum dolor sit amet consectetur adipisicing elit. Minus enim quo magni, sint dolor eligendi illum saepe eum expedita quisquam quis odio vel ea at earum culpa. Dolor, quisquam est! Culpa reiciendis corrupti vitae, quod commodi repellendus amet quo consequuntur.</p>
      <div className="buttons">
        <button className="btn">
          <span>App Store</span>
          <i className="fab fa-apple" />
        </button>
        <button className="btn">
          <span>Google Play</span>
          <i className="fab fa-google-play" />
        </button>
      </div>
    </div>
  </div>
</section>

    )
}

export default AppDisplay;