import {Fragment, useState } from 'react';
import {Link} from 'react-router-dom';
import Logo from '../images/logo.png'

function Header(){
const links=[
{
  value:'Home',
  to:'/'
},
{
  value:'About',
  to:'/about'
},
{
  value:'Faqs',
  to:'/faq'
},
{
  value:'Contact',
  to:'/contact'
},
]
let [menu, menuToggle ]=useState(false)
function HamburgerClick(){
  menuToggle(!menu )
}
    return(
 <Fragment>

  <header id="header">
    <div className="header__logo "> <img src={Logo} className="logo" alt="Chop.Africa Logo" /></div>
    <ul className="header__cta-buttons">
      {
        links.map((link,i)=>{
          return(
            <li key={`${link} - ${i}`} ><Link to={link.to}>{link.value}</Link> </li> 
          )
        })
      }
      
      <li>
      </li><li><a href className="color">Login</a></li>
      <button>Sign Up</button>
    </ul>
<div className={`hamburger ${menu && "change"} `} onClick={HamburgerClick} >
<span className='bar bar-1'></span>
<span className='bar bar-2'></span>
 </div>
  </header>


  <nav className="mobile">
    <div className="items">
      <div className="contacts">
        <div className="f_item">
          <ul className="actions stagger">
            {/* <li><a href="">Sign Up</a></li>
       <li><a href="">Login</a></li> */}
            <li><a href><button>Login</button></a></li>
            <li><a href><button>Sign Up</button></a></li>
          </ul>
          <h4 className="head color stagger">Get in Touch</h4>
          <span className="head stagger">Partnerships &amp; general enquires</span>
          <a href="mailto:hello@chop.africa" className="opaque stagger">hello@chop.africa</a>
          {/* <h5 class="head">Give us a follow</h5> */}
          <ul className="socials">
            <a href><li className="stagger"><span className="fab fa-facebook" /></li></a>
            <a href><li className="stagger"><span className="fab fa-twitter" /></li></a>
            <a href> <li className="stagger"><span className="fab fa-instagram" /></li></a>
          </ul>
        </div>
      </div>
      <ul className="links">
    {
      links.map((link,i)=>{
        return(
          <li className="stagger" key={`${link} - ${i}`}><Link to={link.to}>{link.value}</Link></li>
        )
      })
    }
      </ul>
    </div>
  </nav>
        
</Fragment>
    )
};

export default Header ;